export default {
	data () {
		return {
			currentIndex: true,
	
			//价值
			price: 1,
			textareaMain: ''
			
		}
	},
	onShow(){
		if (uni.getStorageSync('shopInfo')) {
			let shopInfo = uni.getStorageSync('shopInfo')
			console.log(shopInfo)
			if (shopInfo && shopInfo != undefined) {
				this.textareaMain = shopInfo.main
				this.price = shopInfo.price
			}
		}
	},
	methods: {
		//更改下标
		handleCircle(){
			this.currentIndex = !this.currentIndex
		},
		//价值
		priceSub () {
			this.price = this.price - 1
		},
		priceAdd () {
			this.price = Number(this.price) + 1
			if (this.price > 500) {
				uni.showToast({
					icon: 'none',
					title: '价值最高500元，不能再高啦'
				})
				this.price = 500
			}
		},
		//返回
		goBack () {
			if (this.textareaMain.trim() === '') {
				uni.showToast({
					icon: 'none',
					title: '请填写您需要购买的商品信息'
				})
				return
			}
			if (!this.currentIndex) {
				uni.showToast({
					icon: 'none',
					title: '请仔细阅读帮买服务协议'
				})
				return
			}
			//其他
			let shopInfo = {}
			shopInfo.main = this.textareaMain
			shopInfo.price = this.price
			uni.setStorageSync('shopInfo',shopInfo)
			
			uni.navigateBack({
				delta:1
			})
		},
		watchInput() {
			if(this.price > 500) {
				uni.showToast({
					icon: 'none',
					title: '价值最高500元，不能再高啦'
				});
				this.price = 500
			}
		},
		goToRichText (id) {
			uni.navigateTo({
				url: '../rich-text/rich-text?id=' + id
			})
		}
		
	
	
	},
	watch:{
		weight (value) {
			if (value === 0) {
				uni.showToast({
					icon: 'none',
					title: '重量不能再小啦'
				})
				this.weight = 1
			}
		},
		price (value) {
			if (value === 0) {
				uni.showToast({
					icon: 'none',
					title: '价格不能再低啦'
				})
				this.price = 1
			}
		}
	}
}
